import React from "react"

import SEO from "../components/js/SEO"
import Layout from "../components/js/Layout"
import Entry from "../components/js/Entry"

const IndexPage = () => (
  <Layout>
    <SEO title="Blog" lang="de" description="Der Blog von Karl-Katja Krach widmet sich kritisch Philosophie, Politik, Medien und Religion. Außerdem wird hier veröffentlicht, was Karl-Katja Krach sonst so produziert: Musik, Hörspiele, Videos, Texte und was auch immer." keywords={['Blog', 'Kritik', 'Philosophie', 'Politik', 'Gesellschaft', 'Medien', 'Kultur', 'Nachrichten', 'Musik']} />
    <Entry title='Willkommen' link='/page-2/'>
      <p>Hier entsteht die Seite von Karl-Katja Krach.</p>
      <p>Ein Platz für Philosophie und Kritik, Hörspiel und Musik.</p>
    </Entry>
  </Layout>
)

export default IndexPage
